<template>
  <div class="view">
    <van-nav-bar title="" left-text="" left-arrow @click-left="$router.go(-1)"/>
    <div class="content">
      <van-field v-model="form.title" placeholder="()" />
      <van-field v-model="form.reward" placeholder="()" />
      <van-field v-model="form.requires" placeholder="" />
      <van-field v-model="form.description" placeholder="" type="textarea" />

      <van-field
        v-model="form.appType"
        placeholder=""
        :readonly="true"
        :is-link="true"
        @click.native="showCategoryPicker=true"
      />
      <AppTypePicker
        v-model="showCategoryPicker"
        @cancel="showCategoryPicker=false"
        @confirm="handleConfirmAppType"
      />

      <van-field
        v-model="form.projectType"
        placeholder=""
        :readonly="true"
        :is-link="true"
        @click.native="showProjectAppPicker=true"
      />
      <ProjectTypePicker
        v-model="showProjectAppPicker"
        @cancel="showProjectAppPicker=false"
        @confirm="handleConfirmProjectType"
      />

      <van-field
        v-model="form.city"
        placeholder=""
        :readonly="true"
        :is-link="true"
        @click.native="showCityPicker=true"
      />
      <cityPicker v-model="showCityPicker" @on-confirm="onCityChange" />

      <div class="upload-wrap">
        <div class="preview-box" v-for="(item, index) in pickList" :key="index">
          <img class="pre-item" :src="item.content" />
          <div @click="removeItem(index)">
            <div>%{{ item.percent }}</div>
            <img class="icon-remove" src="./img/remove.png" alt="" />
          </div>
        </div>
        <van-uploader class="touch-area" :after-read="onRead">
          <template v-show="pickList.length === 0">
            <img class="icon-add" src="./img/add.png" />
          </template>
        </van-uploader>
      </div>
      <van-cell class="ui-flex" title="">
        <van-switch v-model="checked" />
      </van-cell>
    </div>

    <!--  -->
    <van-button class="btm-fixed" size="large" type="primary" @click="handleSubmit">
      <span></span>
    </van-button>

    <van-dialog v-model="showFormError" title="">
      <ul class="error-list">
        <li v-for="(item, index) in errors" :key="index">{{ index + 1 }}. {{ item }}</li>
      </ul>
    </van-dialog>
  </div>
</template>

<script>
import Vue from 'vue';
import { mapState } from 'vuex';
import {
  Field,
  NavBar,
  Switch,
  Cell,
  CellGroup,
  Uploader,
  Toast,
  Button,
  Dialog
} from 'vant';
import * as qiniu from 'qiniu-js';
import ynowApi from '../../api/ynow';
import { tradeWay } from '../../config';
import cityPicker from '../../components/cityPicker';
import categoryPicker from '../../components/categoryPicker';
import tradeWayPicker from '../../components/tradeWayPicker';
import AppTypePicker from '@/biz-components/app-type-picker';
import ProjectTypePicker from '@/biz-components/project-type-picker';

Vue.use(NavBar)
  .use(Field)
  .use(Switch)
  .use(Cell)
  .use(CellGroup)
  .use(Button)
  .use(Uploader)
  .use(Dialog);

export default {
  components: {
    cityPicker,
    categoryPicker,
    tradeWayPicker,
    AppTypePicker,
    ProjectTypePicker
  },
  data() {
    return {
      checked: '',
      form: {
        reward: '10000',
        title: '',
        city: '',
        description: '',
        requires: 'NodeJS',
        categoryId: '',
        categoryName: '',
        tradeWayId: '',
        tradeWayName: '',
        appType: '',
        projectType: ''
      },
      pickList: [],
      token: '',
      //  picker
      categoryValue: null,
      showCategoryPicker: false,
      //  picker
      tradeWayPickerValue: null,
      showTradeWayPicker: false,
      //  picker
      cityPickerValue: null,
      showCityPicker: false,
      // 
      errors: [],
      showFormError: false,
      showProjectAppPicker: false
    };
  },
  computed: {
    ...mapState({
      categoryList: state => state.xzProduct.category.list
    }),
    tradeWayName() {
      const findIndex = tradeWay.findIndex(element => +element.id === +this.form.tradeWayId);
      if (findIndex !== -1) {
        return tradeWay[findIndex].name;
      } else {
        return '';
      }
    },
    categoryName() {
      const findIndex = this.categoryList.findIndex(element => +element.id === +this.form.categoryId);
      if (findIndex !== -1) {
        return this.categoryList[findIndex].name;
      } else {
        return '';
      }
    }
  },
  mounted() {
    this.doGet7nToken();
    if (this.$route.query.itemId) {
      this.doGetXzProductItem();
    }
  },
  methods: {
    // 
    handleConfirmAppType(obj) {
      this.showCategoryPicker = false;
      this.form.appType = obj.label;
    },

    // 
    handleConfirmProjectType(obj) {
      this.showProjectAppPicker = false;
      this.form.projectType = obj.label;
    },

    // 
    onRead(file) {
      this.pickList.push(file);
      this.uploadImg(this.pickList.length - 1, file.file);
    },

    // 
    removeItem(index) {
      this.pickList.splice(index, 1);
    },

    // token
    async doGet7nToken() {
      try {
        let ret = await ynowApi.get7nToken();
        if (+ret.errCode === 0) {
          this.token = ret.data.token;
        } else {
          Toast(ret.errMsg);
        }
      } catch (error) {
        console.log(error);
      }
    },

    // 
    async doGetXzProductItem() {
      try {
        let ret = await ynowApi.getXzProductItem(this.$route.query.itemId);
        if (+ret.retCode === 0) {
          this.itemInfo = ret.data;
          this.form.reward = +this.itemInfo.reward / 100;
          this.form.title = this.itemInfo.title;
          this.form.city = this.itemInfo.city;
          this.form.description = this.itemInfo.description;
          this.form.requires = this.itemInfo.requires;
          this.form.categoryId = this.itemInfo.category_id;
          this.form.tradeWayId = this.itemInfo.trade_way_id;
          this.pickList = this.itemInfo.imgs.map(element => {
            return {
              url: element,
              content: element,
              percent: 100
            };
          });
        } else {
          Toast(ret.errMsg);
        }
      } catch (error) {
        console.log(error);
      }
    },

    // 
    uploadImg(index, file, callback) {
      const vm = this;
      const observable = qiniu.upload(file, file.name, this.token, {}, {});
      observable.subscribe({
        next(res) {
          vm.pickList[index]['percent'] = res.total.percent;
          vm.pickList = [].concat(vm.pickList);
        },
        error(error) {
          Toast(error);
        },
        complete(res) {
          vm.pickList[index]['url'] = `//c1.airtlab.com/${res.key}`;
          vm.pickList = [].concat(vm.pickList);
        }
      });
    },

    // 
    async doCreate() {
      try {
        let imgs = this.pickList.map(element => element.url);
        imgs = JSON.stringify(imgs);
        const params = {
          ...this.form,
          imgs: imgs,
          reward: +this.form.reward * 100
        };
        console.log(params);
        let ret = await ynowApi.createDemand(params);
        if (+ret.errCode === 0) {
          Toast.success('');
        } else {
          Toast(ret.errMsg);
        }
      } catch (error) {
        console.log(error);
      }
    },

    // 
    async doUpdateProduct() {
      try {
        let imgs = this.pickList.map(element => element.url);
        imgs = JSON.stringify(imgs);
        const params = {
          ...this.form,
          imgs: imgs,
          itemId: this.$route.query.itemId,
          reward: +this.form.reward * 100
        };
        let ret = await ynowApi.updateXzProduct(params);
        if (+ret.errCode === 0) {
          Toast.success('');
        } else {
          Toast(ret.errMsg);
        }
      } catch (error) {
        console.log(error);
      }
    },

    // 
    handleSubmit() {
      const form = this.form;
      const errors = [];
      if (form.title.length < 5) {
        errors.push('5');
      }
      if (form.reward.length === 0) {
        errors.push('');
      }
      if (form.city.length === 0) {
        errors.push('');
      }
      // if (form.requires.length === 0) {
      //   errors.push('')
      // }
      // if (form.categoryId.length === 0) {
      //   errors.push('')
      // }
      // if (form.tradeWayId.length === 0) {
      //   errors.push('')
      // }
      // if (this.pickList.length < 2) {
      //   errors.push('2')
      // }
      this.errors = errors;
      this.showFormError = !!errors.length;
      if (!this.showFormError) {
        this.doCreate();
        // if (this.$route.query.itemId) {
        //   this.doUpdateProduct()
        // } else {
        //   this.doCreateProduct()
        // }
      }
    },

    // 
    onCategoryChange(data) {
      this.categoryValue = data;
      this.form.categoryId = data.id;
      this.form.categoryName = data.name;
      this.showCategoryPicker = false;
    },

    // 
    onTradeWayChange(data) {
      this.tradeWayPickerValue = data;
      this.form.tradeWayId = data.id;
      this.form.tradeWayName = data.name;
      this.showTradeWayPicker = false;
    },

    // 
    onCityChange(data) {
      this.cityPickerValue = data;
      let values = [];
      data.forEach(element => {
        values.push(element.name);
      });
      this.form.city = values.join(' ');
    }
  }
};
</script>

<style lang="scss" scoped>
.view {
  padding-bottom: 50px;
  .btm-fixed {
    z-index: 100;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
  }
}

.ui-flex {
  display: flex;
  align-items: center;
  /deep/ .van-cell__value {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.error-list {
  padding: 14px 20px;
  font-size: 14px;
  color: #999;
}

// 
.upload-wrap {
  float: right;
  padding: 20px 0;
  .touch-area,
  .pre-item {
    float: right;
    width: 80px;
    height: 80px;
    margin-right: 10px;
    margin-bottom: 10px;
    border: 1px solid #eee;
  }
  .icon-add {
    position: absolute;
    width: 30px;
    height: 30px;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
  }
  .icon-remove {
    display: block;
    width: 24px;
    height: 24px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .preview-box {
    float: right;
  }
}

.btm-fixed {
  position: absolute;
  bottom: 0;
}
</style>



