<template>
  <div class="popup" v-if="value">
    <van-picker
      ref="picker"
      class="picker"
      show-toolbar
      :title="title"
      :columns="list"
      value-key="label"
      @cancel="onCancel"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      list: [{
        label: ''
      }, {
        label: ''
      }]
    };
  },
  methods: {
    onCancel() {
      this.$emit('cancel');
    },
    onConfirm(data) {
      this.$emit('confirm', data);
    }
  }
};
</script>

<style lang="scss" scoped>
.popup {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .4);
}
.picker {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
}
</style>



